.about{
    background-color: #f4f4f8;
    /* display: flex; */
    /* justify-content: center; */
    /* color: #dfe0e3; */
    /* align-items: center; */
    /* background-image: linear-gradient(to right,rgb(234, 120, 194),pink,rgb(226, 147, 29)); */
    margin-left: 13vw;
    width: 87vw;
    height: 99.5vh;
    
 }
 .about>.head{
    width: 100%;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    height:10vh ;
    color: #fff;
    background-image: linear-gradient(to right,rgb(184, 27, 129),rgb(184, 37, 61),rgb(233, 174, 86));
    background-color: #ec7abb;
 }
 .about>p{
    padding: 15px;
    font-size: x-large;
 }
 .about>p>p>span{
    font-weight: 900;
 }

 @media only screen and (max-width: 600px) {
   .about{
     width: 99vw;
     margin-left: 0vw;
   }
   .about>.head{
      margin-top: 8vh;
   }
 }