.project{
    background-color: #f4f4f8;
    
    margin-left: 13vw;
    width: 87vw;
    height: 99.5vh;
    
 }
 .project>.head{
    width: 100%;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    height:10vh ;
    color: #fff;
    background-image: linear-gradient(to right,rgb(184, 27, 129),rgb(184, 37, 61),rgb(233, 174, 86));
    background-color: #ec7abb;
 }
 .project .cpp,.webdev,.android{
    width: 24vw;
    display: flex;
    flex-direction: column;
    height: 50vh;
    border: 2px solid black;
    margin-top: 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 30px;
 }
 .projcontent{
    display:flex;
    justify-content: space-around;
    padding: 80px;
 }
 .projcard>span{
    flex: .2;
    font-size: x-large;
    border-bottom:2px solid #171b3b;
    display: block;
    z-index: 55;
    position: sticky;
    display: flex;
    align-items: center;
    justify-content: center;
 }
 .projcard>img{
    flex: .6;
    z-index: 5;
    padding: 10px;
 }
 .projcard>.radio{
    flex: .2;
    padding: 10px;
    margin: auto;
    /* position: fixed; */
    z-index: 55;
 }


 @media only screen and (max-width: 600px) {
   .project{
     width: 99.5vw;
     margin-left: 0vw;
   }
   .project>.head{
      margin-top: 8vh;
   }
 }