.education{
    background-color: #f4f4f8;
    
    margin-left: 13vw;
    width: 87vw;
    height: 99.5vh;
    
 }
 .education>.head{
    width: 100%;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    height:10vh ;
    color: #fff;
    background-image: linear-gradient(to right,rgb(184, 27, 129),rgb(184, 37, 61),rgb(233, 174, 86));
    background-color: #ec7abb;
 }
 .education>.cardcontainer{
    width: 100%;
    display:flex;
    
    /* grid-template-columns: repeat(2,1fr); */
    width: 87vw;
    height: 89.5vh;
    border: 1px solid black;
 }
  .education>.cardcontainer>.card{
    width: 30vw;
    /* margin: 20px auto; */
    /* margin-right: 20px; */
    margin-left: 20px;
    margin-top: 20px;
    height: 40vh;
    border: 2px solid black;
  }
  .education>.cardcontainer>.grad
  {
    display: block;
  }


  @media only screen and (max-width: 600px) {
   .education{
     width: 99.5vw;
     margin-left: 0vw;
   }
   .education>.head{
    margin-top: 8vh;
   }
 }