.contact{
    background-color: #f4f4f8;
    /*  */
    /* display: flex;
    flex-direction: column;
    align-items: center; */
    margin-left: 13vw;
    width: 87vw;
    padding-bottom: 10px;
    height:99.5vh ;
    
 }
 .contact>.head{
    width: 100%;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    height:10vh ;
    color: #fff;
    background-image: linear-gradient(to right,rgb(184, 27, 129),rgb(184, 37, 61),rgb(233, 174, 86));
    background-color: #ec7abb;
 }
 .contactcont{
    margin-left: 3em;
 }
 .contactcont>p{
    font-size: x-large;
    padding: 25px;
    display: flex;
 }
 .contactcont>p>span{
    margin-left: 1em;
 }
 .contactcont>p>.contacticon{
    color: #fff;
    border-radius: 50%;
    font-size: 35px;
    padding: 10px;
    background-color: rgb(51, 51, 91);
    transition: background-color .5s ease-out;
 }
 .contactcont>p>.contacticon:hover{
    color: rgb(11, 125, 255);
    background-color: #8c8c8c;
 }


 @media only screen and (max-width: 600px) {
   .contact{
     width: 99.5vw;
     margin-left: 0vw;
     /* margin-top: 15vw; */
   }
   .contact>.head{
      margin-top: 8vh;
   }
 }